.top-nav{
    //background-image: url("../images/woodfloorBG.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    height: 55px;
    margin-bottom: 20px;
    padding-bottom: 20px;
    
        @media (min-width: 900px){justify-content: center;}

        @media (min-width: 1150px){justify-content: flex-end;}
    

}

.nav{
    display: none;
    position: relative;
    color: @2nd-color;
    z-index: 1;
    font-family: 'Source Sans Pro', sans-serif;


        @media (min-width: 900px){position: relative; display: flex;}

        @media (min-width: 1150px){}

        ul{
            list-style: none;
            margin: 20px;
            display: flex;
            width: 780px;
            justify-content: space-between;
            
                    li{
                        margin-left: 20px;

                        &:hover{color: @1st-color;}

                        &:active{text-decoration: underline;}
             

        }
    }
}

.sub-nav{
    display: flex;
    justify-content: space-between;
    padding: 10px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    height: 50px;
    z-index: 1;
    opacity: 1;

        @media (min-width: 900px){height: 40px; opacity: 0;}

        div{
            font-size: 14px;
            align-self: left;


                @media (min-width: 480px){display: flex;}
                    
                    p{
                        align-self: right;
                        margin: 0px;
                        margin-left: 30px;
                        
                    }
            }

        .icon-navicon{
            z-index: 1;
            font-size: 36px;

                @media (min-width: 1150px){opacity: 0;}

                    &:active{color: @1st-color;}
        }
}


.side-nav{
        background-color: @3rd-color;
        color: @2nd-color;
        width: 250px;
        position: absolute;
        top: 50px;
        left: -250px;
        z-index: 3;
        transition: left 0.25s;
        height: 100%;

        @media (min-width: 1150px){display: none;}
    
            ul{
                list-style: none;
                padding: 0px;
                margin: 0px;


                    li{
                        
                        padding: 10px 0px 10px 20px;
                        background-color: rgba(117,82,71, 0.5);
                        //border-bottom: 1px solid @1st-color;

                        &:hover{background-color: @4th-color;
                        color:@2nd-color;}

                        &:active{color: @1st-color}
                    }
            }
}

.move{
    left: 0px;
}

 a{
    color: @2nd-color;
    text-decoration: none;
    font-family: 'Source Sans Pro', sans-serif;
}

    a:visited {color: @3rd-color;}

    a:hover {color: @1st-color;}

    a:active {text-decoration: underline;}