.whyMont-oneContainer{
    width: 75%;
    align-self: center;
    display: flex;
    flex-direction: column;

                
                
        div:nth-child(1){
            display: flex;
            background-color: @3rd-color;
            color: @5th-color;

            h3{color:@4th-color;}
            p{color:@2nd-color;}

        }

        div:nth-child(2){
            display: flex;
            flex-direction: row-reverse;
            background-color: @4th-color;
            color: @3rd-color;

            h3{color:@2nd-color;}
        }

        div:nth-child(3){
            display: flex;
            background-color: @3rd-color;
            color: @5th-color;

            h3{color:@4th-color;}
            p{color:@2nd-color;}

        }


        div:nth-child(4){
            display: flex;
            flex-direction: row-reverse;
            background-color: @4th-color;
            color: @3rd-color;

            h3{color:@2nd-color;}
        }

}

.oneContainer{
    display: flex;
    flex-direction: row;

        .oneContainer-img{
            margin: 0px;
            width: 50%;
            height: 350px;

            
            img{            
                width: 100%;
                height: 100%;
                }

        }

        .oneContainer-text{
            width: 50%;
            padding: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;

            h3{        
       	        font-family: 'Lora', serif; 
                margin: 0px;
            }

            p{
                font-family: 'Source Sans Pro', sans-serif;
                line-height: 26px;
                word-spacing: 3px;

            }

        }

}


